	/*----------------------drop down menu----------------------*/

.drop_down_button {
  background-color: black;
  height: 20px;
}
.drop_down_menu {
  diplay: none;
  position: absolute;
  top: 20px;
  left: 0px;
  background-color: #000000;
}
.drop_down_content {
  font: 400 16px "Montserrat", sans-serif;
  color: #ccbdb3;
  margin: 0;
}
.drop_down_content_highlight {
  font: 400 16px "Montserrat", sans-serif;
  color: #ccbdb3;
  margin: 0;
}
.drop_down_bar:hover {
  transition: .3s ease;
  background-color: white;
}
.drop_down_bar:hover .language_menu{
  transition: .3s ease;
  diaplay: bloc;
}



